import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {Modal, Menu, Icon, Dropdown} from 'antd';  
import Scrollbars from 'react-custom-scrollbars';

import './change_desk_dishes_popup.less';

@inject('dineStore') @observer
class ChangeDeskDishes extends Component {

    handleOk=()=>{
        let { dineStore } = this.props;
        let mainTableIDs=[], viceTableIDs=[];

        dineStore.occupiedTableList.forEach(table => {
            let targetTableID = table.targetTableID ? table.targetTableID : -1;
            mainTableIDs.push(table.tableID);
            viceTableIDs.push(targetTableID);
        })

        dineStore.turnTable({
            bookingID: dineStore.occupiedTableList[0].bookingID, 
            mainTableIDs,
            viceTableIDs
        });

        let { closeHandle } = this.props;
        closeHandle && closeHandle();
    }

    handleCancel=()=>{
        let { closeHandle } = this.props;
        closeHandle && closeHandle();
    }

	render() {
        let { dineStore } = this.props;
        return <div>
			<Modal  
                title="-"
				visible={true} 
				maskClosable={false} 
				okText='' cancelText=''
				width={700}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
				wrapClassName="change-desk-popup-modal"    
				>      
				<div className="change-desk-container">
					<div className="title"></div>
                    <div className="content">
                        <Scrollbars>
                            {dineStore.occupiedTableList && !!dineStore.occupiedTableList.length &&
                            dineStore.occupiedTableList.map((table, index) => {
                                return <div key={index} className="items">
                                    <div className="deskName">{table.tableName}{table.dishesQuantity}</div>
                                    <div className="text"></div>                             
                                    <Dropdown 
                                        overlay={<Menu onClick={(ele) => {
                                                let targetTable = JSON.parse(ele.key);
                                                dineStore.setExchangeTable({
                                                    tableID: table.tableID,
                                                    targetTableName: targetTable.tableName,
                                                    targetTableID: targetTable.tableID
                                                });
                                            }}>
                                                {dineStore.targetTableList && dineStore.targetTableList.length &&
                                                dineStore.targetTableList.map((targetTable, index) => {
                                                    return <Menu.Item key={JSON.stringify(targetTable)}>{targetTable.tableName}</Menu.Item>
                                                })}
                                            </Menu>} 
                                        wrapClassName="my-dropdown"
                                    >
                                        <div className="button">
                                            {table.targetTableName}<Icon type="down" />
                                        </div>
                                    </Dropdown>
                                </div>
                            })}
                        </Scrollbars>
                    </div>
				</div>
			</Modal>
        </div>
	}

}

export default ChangeDeskDishes;



