import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import classnames from 'classnames';
import { Tabs, message } from 'antd';

import TableWindow from './TableWindow';
import PromptPopup from 'components/prompt-popup';
import Prompt from 'components/prompt-common';
import Scrollbars from 'react-custom-scrollbars';
import SelectMealPopup from './select-meal-popup';

import './modal.css';
import 'components/scroll-tabs/dine_scroll_tabs.css';

const TabPane = Tabs.TabPane;

message.config({
  top: 300
});

const iconSearch={
  top: '3px',
  right: '23px',
  cursor: 'pointer',
  height: '30px',
  lineHeight: '30px',
  color: '#9b9b9b',
  fontSize: '16px',
}

const promptContStyle = {
  lineHeight: '200px',
  textAlign: 'center',
  color: '#000',
  fontSize: '20px'
}

function Table({ dineStore, table }) {
  let related = !dineStore.targetView && dineStore.selectedTableList && 
  dineStore.selectedTableList.length && dineStore.selectedTableList[0].orderID && 
  table.orderID && (table.tableStatus === 827 ||  table.tableStatus === 652 || 
  table.tableStatus === 728) && dineStore.selectedTableList[0].combineNumber > 1 && 
  table.orderID === dineStore.selectedTableList[0].orderID;
  return <div className={classnames({
          'item-basic': true,
          'item-reserve': table.tableStatus === 606, //
          'item-not-order': table.tableStatus === 827, //
          'item-has-order': table.tableStatus === 652, //
          'item-zanjie': table.tableStatus === 728, //
          'item-dirty': table.tableStatus === 609, //
          'item-free': table.tableStatus === 607, //
          'item-service': table.tableStatus === 610, //
          'item-selected': table.selected,
          'item-related': related,
          'item-pintai': table.shareNumber
        })} onClick={() => {
          if(dineStore.multipleSelected && table.selected) {
            //
            dineStore.delSelectedTable(table.tableID);
          }else {
            if(!table.selected) {
              //
              dineStore.getSelectedTable(table.tableID, table.tableStatus, table.subOrderID);
            }
          }
          //,
          if(!dineStore.targetView) dineStore.changeTableWindow('base');
        }}>
      <div className="item-gou"><i className='iconfont icon-home_icon_selected'></i></div>
      <div className="item-link"><i className='iconfont icon-home_icon_link'></i></div>
      <div className="item-content">
          <div className={classnames({'item-title': true, 'item-title-small': table.tableName.length>6})}>
            {table.shareTableName && table.tableName !== table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName}
          </div>
          <div className="item-number">
          {table.customerNumber || 0}/{table.defaultPerson}
          {(() => {
            if(table.areaType === 850) return <span className="item-bao"></span>;
          })()}
          {table.combineName ? <span className="item-lian">{table.combineName}</span> : null}
          </div>
      </div>
      <div className="item-pins">
        {(() => {
          if (table.price) {
            return <div className='item-price'>{''+table.price}</div>;
          }else if(table.times){
            return <div className='item-time'>{table.times+''}</div>;
          }
        })()}
      </div>
  </div>
}

function StatusButton({ dineStore, status }) {
  let buttonId,circleClass,statusName,circle;
  switch(status.status) {
    case 606:
      buttonId = 'btn_reverse';
      circleClass = 'reserveBg';
      statusName = '';
      break;
    case 827:
      buttonId = 'btn_not_order';
      circleClass = 'on_orderBg';
      statusName = '';
      break;
    case 652:
      buttonId = 'btn_has_order';
      circleClass = 'orderBg';
      statusName = '';
      break;
    case 728:
      buttonId = 'btn_zanjie';
      circleClass = 'pauseBg';
      statusName = '';
      break;
    case 609:
      buttonId = 'btn_dirty';
      circleClass = 'dirtyBg';
      statusName = '';
      break;
    case 607:
      buttonId = 'btn_free';
      circleClass = 'freeBg';
      statusName = '';
      break;
    case 610:
      buttonId = 'btn_service';
      circleClass = 'maintainBg';
      statusName = '';
      break;
    default:
      buttonId = 'btn_all';
      statusName = '';
  }

  if(status.status !== 0){
    circle = <span className={classnames({
      'btn_circle': true,
      [circleClass]: true,
    })}></span>
  }
  return <li
    id={buttonId}
    className={classnames({
      'btn-active': status.status === dineStore.currentStatus
    })}
    onClick={() => {
      //
      dineStore.getTableListByStatus({
        areaID: dineStore.currentAreaID,
        status: status.status
      });
    }}>
    {circle}
    {statusName}(<span>{status.count}</span>)
  </li>
}

@inject('appStore','dineStore') @observer
class Dine extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      statePopup: false
    }

    let { dineStore } = props;
    dineStore.getAreaList(); //
  }

  //
  selectAllTables() {
    this.props.dineStore.selectAllTables();
  }

  componentDidMount() {
    let appStore = this.props.appStore;
    let _this = this;
    
    //
    appStore.checkBeforeDailyWorking(function() {
      //
      _this.setState({
        statePopup: <PromptPopup 
          okText="" 
          cancelText=""
          onOk={() => {
            _this.context.router.push('/day-end');
            _this.setState({statePopup: false});
          }}
          onCancel={() => {
            _this.setState({statePopup: false});
          }}
        >
          <div style={promptContStyle}></div>
        </PromptPopup>
      });
    }); 

    appStore.isInWorking(); //
  }

  componentDidUpdate() {
    let dineStore = this.props.dineStore;
    let feedback = dineStore.feedback;
    if(feedback && feedback.status !== 'error' && feedback.status !== 'validate') {
      //
      switch(feedback.status) {
        case 'success':
          message.success(feedback.msg,dineStore.closeFeedback());
          break;
        case 'warn':
          message.warn(feedback.msg,dineStore.closeFeedback());
          break;
        default:
          message.info(feedback.msg,dineStore.closeFeedback());
      }
    }
  }

  componentWillUnmount() {
    let { dineStore } = this.props;
    sessionStorage.setItem('selectedTableList', JSON.stringify(dineStore.selectedTableList)); //sessionStorage
    dineStore.clearView(); // 
  }

	render() {
    let dineStore = this.props.dineStore;
    let feedback = dineStore.feedback;
    let operatePrompt,selectMealPopup;
    if(feedback && feedback.status === 'error') {
      //
      feedback.okClick = () =>{
        if(feedback.confirmClick) feedback.confirmClick();
        feedback.cancelClick();
      }
      feedback.cancelClick = () =>{
        dineStore.closeFeedback();
        //
        dineStore.currentTableList.length && dineStore.refreshView();
      }
      operatePrompt = <Prompt message={feedback} />
    }

    if(dineStore.mealList && dineStore.mealList.length) selectMealPopup = <SelectMealPopup />;

    let bookingID = this.props.location.state ? this.props.location.state.bookingID : '';

		return <div id="container">
        <div id="left_block">
            <div id="main_top">
    					<div className="menuNav">
                  <div id="menuWrap">
                    <Tabs activeKey={dineStore.currentAreaID} onTabClick={(areaID) => {
                      //
                      dineStore.getStatusListByArea(areaID);
                    }}>
                    {dineStore.areaList &&
                    !!dineStore.areaList.length && dineStore.areaList.map((area, index) =>
                      <TabPane tab={area.areaName} key={area.areaID}></TabPane>
                    )}
                    </Tabs>
                  </div>
              </div>
              <div id="search" className="pr">
                  <input type="text" id="enterSearch" placeholder="" value={dineStore.searchName} onChange={(e) => {
                    //
                    dineStore.getTableListByTableName({tableName: e.target.value});
                  }} />
                  <i className="iconfont icon-xinzenghuiyuan_icon_sousuo pa" style={iconSearch}></i>
              </div>
            </div>

            <div id="main_middle">
              <Scrollbars>
                {dineStore.tableList && !!dineStore.tableList.length && dineStore.tableList.map((table, index) =>
                  <Table dineStore={dineStore} table={table} key={index} />
                )}
              </Scrollbars>
            </div>

            <div id="main_bottom">
                <ul id="btn" className="clearfix">
                    {dineStore.statusList && 
                    !!dineStore.statusList.length &&
                    dineStore.statusList.map((status, index, arr) =>
                      <StatusButton key={index} dineStore={dineStore} status={status} />
                    )}
                </ul>
                <div id="leftBot_right">
                    {false && <p id="allSelected" className={dineStore.areAllTableSelected?'selected':null} onClick={this.selectAllTables.bind(this)}><i className="circle"></i></p>}
                    {false && <p id="join"><i className="circle selected"></i></p>}
                </div>
            </div>

        </div>
        <div id="right_block">
          <TableWindow bookingID={bookingID} />
          <div id="desk_info_footer">
            <div className="left-block">
                <span></span>
            </div>
            <div className="right-block">
                <div>
                    <span>256</span>
                    <span className="tai-number">45</span>
                </div>
                <div className="all-money">1000</div>
            </div>
          </div>
        </div>
        {this.state.statePopup}
        {operatePrompt}
        {selectMealPopup}
    </div>
	}

}

Dine.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}

export default Dine;



